import { Component, OnInit } from '@angular/core';
import {HttpServiceService}from '../http-service.service'
import { ActivatedRoute, Router } from '@angular/router';
import{NgxSpinnerService} from 'ngx-spinner'

@Component({
  selector: 'app-track',
  templateUrl: './track.component.html',
  styleUrls: ['./track.component.css']
})
export class TrackComponent implements OnInit {
  public gamerTag:any;
public device:any;
  constructor(public httpService:HttpServiceService,public router:Router,private spinner:NgxSpinnerService) { }

  ngOnInit() {

    
    console.log("called")
    
  }
  public get=()=>{
    this.spinner.show();
    setTimeout(()=>{
      this.spinner.hide()
    },10000)
    console.log(this.device,this.gamerTag)
    this.httpService.getsingle(this.device,this.gamerTag).subscribe(
      data=>{
        this.httpService.setUserInfoInLocalStorage(data)

        console.log(data)
        console.log('subscribed')
        this.router.navigate(['/view'])
        
      },
      error=>{
        console.log(error)
        setTimeout(()=>{
          this.router.navigate(['/error'])
           },3000 )
      }
    )
  }

}
